:root {
    /* colors */
    --white: #FFF;
    --black: #000;
    --accent: #2BA9E0;
    --gark-blue: #071D34;
    --green: #078C08;
    --gray: #CCC;
}

html, body, form, fieldset, ul, dl, dt, dd, table, td, th, img, menu {
    margin: 0;
    padding: 0;
    border: none;
}

html {
    height: 100%;
    width: 100%;
    overflow: hidden;
    user-select: none;
}

body {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    width: 100%;
    height: 100%;
    font: 2.4rem/1.5 'Tahoma', sans-serif;
}

.app-container {
    display: grid;
    grid-template-columns: 50rem 92rem 50rem;
    background-color: #2B6C55;
    box-shadow: inset 0 0 0.1rem 1rem rgba(0, 0, 0, .3);
}

.blocker {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
}